body {
  margin: 0;
  background-color: rgb(235, 235, 230);
  font-size: 13px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

button {
  cursor: pointer;
  background-color: #fff;
}

button,
button:active,
button:focus {
  outline: none;
  border: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

a:focus {
  outline: none;
}

.container {
  width: 940px;
  height: 422px;
  margin: 0 auto;
  margin-top: 300px;
  display: flex;
  background-color: #fff;
}

/* sidebar */

.sidebar {
  border-right: 1px solid #eff2f7;
}
.sidebar-buttons {
  width: 226px;
  height: 234px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.sidebar button {
  height: 100%;
  background-color: #fff;
  text-align: left;
  padding: 0 20px;
  cursor: pointer;
  border-bottom: 1px solid #eff2f7;
  font-size: 12px;
  font-family: Arial;
}

.sidebar button:hover {
  background-color: #eff2f7;
}

.sidebar .btn-active {
  color: rgb(61, 145, 255);
  font-weight: 700;
}

/* content */

.content {
  width: 100%;
}

.header {
  height: 57px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: #eff2f7;
}

.header button {
  height: 35px;
  width: 47%;
  cursor: pointer;
  color: #849095;
  border-radius: 5px;
}

.header .btn-selected {
  border: 1px solid rgb(61, 145, 255);
}

.month-slider {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
}

.month-slider button {
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 5px;
  background: #eff2f7;
}

.month-slider button:hover {
  background: #e1e7f0;
}

.date-picker {
  display: flex;
}

.date-picker span:first-child {
  margin-right: 15px;
}

.date-picker select {
  display: block;
  font-size: 12px;
  appearance: none;
  background-color: #fff;
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
  border: 0;
  padding: 10px 30px 10px 10px;
  border-radius: 4px;
  outline: 0;
  color: #3e484f;
  cursor: pointer;
  text-align: center;
}

.date-picker select:hover {
  background-color: rgba(0, 0, 0, 0.07);
}

/* calendars */

.calendars {
  display: flex;
  justify-content: space-evenly;
}

.calendar {
  width: 46%;
}

.selected-month {
  padding: 10px 0;
  font-weight: 600;
  color: #849095;
  padding-left: 12px;
}

.week-days {
  color: rgb(132, 144, 149);
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: 32px;
  text-align: center;
  align-items: center;
}

.days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(6, 36px);
}

.days button {
  font-size: 12px;
  position: relative;
}

.days .passive {
  pointer-events: none;
  color: rgb(191, 191, 192, 0.7);
}

.days span {
  position: absolute;
  left: 0;
  right: 0;
  top: 5px;
  bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.days button:hover {
  border: 1px solid rgb(61, 145, 255);
  border-radius: 15px;
}

.days .hovered {
  border-top: 1px solid rgb(61, 145, 255);
  border-bottom: 1px solid rgb(61, 145, 255);
}

.days .selected {
  background-color: rgb(61, 145, 255);
  color: #fff;
}

.days .selected:hover {
  color: rgb(4, 243, 251);
}

.days .leftEdge {
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  left: 2px;
}

.days .rightEdge {
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  right: 2px;
}

.days .selected.hovered {
  color: rgb(247, 251, 4);
}

.days .today {
  font-weight: bold;
}

.days .today span:after {
  content: '';
  position: absolute;
  bottom: 4px;
  left: 50%;
  transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 18px;
  height: 2px;
  border-radius: 2px;
  background: #3d91ff;
}

.days .today .selected {
  background: rgb(23, 217, 252);
}
